html {
  font-size: 100%;
} /*16px*/

body {
  font-family: $font_base;
  font-size: $font_size;
  font-weight: 300;
  line-height: 1.65em;
  color: palette(fg);
}

p {
  margin-bottom: 1rem;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 3rem 0 1.38rem;
  font-weight: 500;
  line-height: 1.3;
  font-family: $font_heading;
}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

small,
.text_small {
  font-size: 0.8rem;
}
